<template>
  <teleport to="body">
    <transition name="dialog">
      <div
        class="position-fixed bottom-0 end-0 start-0 p-4"
        style="z-index: 9999"
        v-if="visibility"
      >
        <div
          id="toast"
          class="toast show"
          role="alert"
          data-bs-autohide="false"
          aria-live="assertive"
          aria-atomic="true"
          :style="toastType"
        >
          <div class="d-flex justify-content-between">
            <div class="toast-body text-white">
              {{ message }}
            </div>
            <button
              type="button"
              class="btn-close btn-close-white me-2 m-auto"
              aria-label="Close"
              @click="closeToast"
            ></button>
          </div>
        </div>
      </div>
    </transition>
  </teleport>
</template>

<script>
export default {
  props: ["message", "visibility", "type"],
  emits: ["close"],
  computed: {
    toastType() {
      if (this.type === "success") {
        return "background-color: rgb(74 222 128)";
      } else if (this.type === "error") {
        return "background-color: rgb(248 113 113)";
      } else {
        return "background-color: rgb(163 163 163)";
      }
    },
  },
  methods: {
    closeToast() {
      this.$emit("close");
    },
  },
  watch: {
    visibility() {
      if (this.visibility) {
        setTimeout(() => {
          this.closeToast();
        }, 3000);
      }
    },
  },
};
</script>

<style scoped>
.dialog-enter-from,
.dialog-leave-to {
  opacity: 0;
  transform: scale(0.8);
}

.dialog-enter-active {
  transition: all 0.3s ease-out;
}
.dialog-leave-active {
  transition: all 0.3s ease-in;
}

.dialog-enter-to,
.dialog-leave-from {
  opacity: 1;
  transform: scale(1);
}
</style>