export default {
  state() {
    return {
      token: null,
    };
  },
  getters: {
    getToken: (state) => state.token,
  },
  mutations: {
    setToken(state, token) {
      state.token = token;
    },
  },
  actions: {
    setToken({ commit }, token) {
      commit("setToken", token);
    },
    checkIsLogin({ commit }) {
      const token = localStorage.getItem("token");
      if (token) {
        commit("setToken", token);
      }
    },
  },
};
