export default {
  state() {
    return {
      header: "",
      navTabIsActive: false,
      isNavShow: false,
    };
  },
  getters: {
    getHeader: (state) => state.header,
    getNavTabIsActive: (state) => state.navTabIsActive,
    getIsNavShow: (state) => state.isNavShow,
  },
  mutations: {
    setHeader(state, header) {
      state.header = header;
    },
    setNavTabIsActive(state, navTabIsActive) {
      state.navTabIsActive = navTabIsActive;
    },
    setIsNavShow(state, isNavShow) {
      state.isNavShow = isNavShow;
    },
  },
  actions: {
    setHeader({ commit }, header) {
      commit("setHeader", header);
    },
    setNavTabIsActive({ commit }, navTabIsActive) {
      commit("setNavTabIsActive", navTabIsActive);
    },
    setIsNavShow({ commit }, isNavShow) {
      commit("setIsNavShow", isNavShow);
    },
  },
};
