import axios from "axios";
import { configUrl, changeNoHp } from "@/store/setup/config.js";

const url = configUrl("kemitraan");

export default {
  namespaced: true,
  state() {
    return {
      mitra: {
        nama: "",
        image: "",
        kodeAgen: "",
        tglDaftar: "",
        nomorHp: null,
        jmlMitra: 0,
      },
      detailXp: {
        holdPosition: 0,
        minXp: 0,
        maxXp: 0,
        currentXp: 0,
      },
    };
  },
  getters: {
    getMitra: (state) => state.mitra,
    getCurrentXp: (state) => state.detailXp.currentXp,
    getPositionXp: (state) => {
      const { currentXp, maxXp, holdPosition } = state.detailXp;
      let paramBagi;
      if (maxXp === 999999999 && currentXp > holdPosition) {
        return 100;
      } else if (maxXp === 999999999 && currentXp < holdPosition) {
        paramBagi = holdPosition + 30000;
        const position = (currentXp / paramBagi) * 100;
        return position;
      }
      const position = (currentXp / maxXp) * 100;
      return position;
    },
    isHold: (state) => {
      const { holdPosition, currentXp, maxXp } = state.detailXp;
      if (currentXp < holdPosition) {
        const neededXp = holdPosition - currentXp;
        return `Butuh ${neededXp}XP Untuk Bertahan!`;
      } else {
        if (maxXp === 999999999) {
          return "Level Anda sudah maksimal";
        } else {
          const neededXp = maxXp - currentXp;
          return `Butuh ${neededXp}XP Untuk Naik!`;
        }
      }
    },
  },
  mutations: {
    setMitra(state, mitra) {
      state.mitra = mitra;
    },
    setDetailXp(state, detailXp) {
      state.detailXp = detailXp;
    },
  },
  actions: {
    async getMitra(context) {
      const sendData = {
        wtinkouridol: context.rootGetters.getToken,
      };

      const response = await axios.post(url, JSON.stringify(sendData), {
        headers: {
          "Content-Type": "application/json",
        },
      });

      const responseData = response.data.data;

      if (response.data.status !== 200) {
        const error = new Error(responseData.message || "Gagal mendapatkan data mitra!");
        throw error;
      }

      const mitra = {
        nama: responseData[0].nama,
        image: responseData[0].img_logo,
        kodeAgen: responseData[0].kode,
        jmlMitra: responseData[0].jml_mitra,
        tglDaftar: responseData[0].tgl_daftar,
        nomorHp: changeNoHp(responseData[0].pengirim),
      };

      const detailXp = {
        holdPosition: responseData[0].hold_position,
        minXp: responseData[0].minimal_xp,
        maxXp: responseData[0].maximal_xp,
        currentXp: responseData[0].jumlah_xp,
      };

      context.commit("setMitra", mitra);
      context.commit("setDetailXp", detailXp);
    },
  },
};
