import axios from "axios";
import { configUrl } from "@/store/setup/config.js";

const url = configUrl("levelMitra");

export default {
  namespaced: true,
  state() {
    return {
      level: [],
    };
  },
  getters: {
    getLevel: (state) => state.level,
    getPosition: (state) => {
      const { level } = state;
      // get index of position 1
      const index = level.findIndex((item) => item.posisi === 1);
      return index;
    },
  },
  mutations: {
    setLevel(state, level) {
      state.level = level;
    },
  },
  actions: {
    async getLevel(context) {
      const sendData = {
        wtinkouridol: context.rootGetters.getToken,
      };

      const response = await axios.post(url, JSON.stringify(sendData), {
        headers: {
          "Content-Type": "application/json",
        },
      });

      const responseData = response.data;

      if (response.status !== 200) {
        const error = new Error(responseData.message || "Gagal mendapatkan level!");
        throw error;
      }

      context.commit("setLevel", responseData);
    },
  },
};
