const configUrl = (parameter) => {
  return `http://api.murapay.id:5758/api/external/${parameter}`;
  //return `http://192.168.5.12:3000/api/external/${parameter}`;
};

const changeNoHp = (value) => {
  if (value.includes("+62")) {
    return value.replace("+62", "0");
  } else {
    return value;
  }
};

export { configUrl, changeNoHp };
