<template>
  <the-header :title="title" :link="link"></the-header>
  <the-navigation-button v-show="isNavShow"></the-navigation-button>
  <router-view v-slot="slotProps">
    <transition name="route" mode="out-in">
      <component :is="slotProps.Component"></component>
    </transition>
  </router-view>
</template>

<script>
import TheHeader from "./components/layouts/TheHeader.vue";
import TheNavigationButton from "./components/layouts/TheNavigationButton.vue";

export default {
  name: "App",
  components: {
    TheHeader,
    TheNavigationButton,
  },
  computed: {
    title() {
      return this.$store.getters.getHeader;
    },
    link() {
      const navIsActive = this.$store.getters.getNavTabIsActive;
      const token = this.$store.getters["getToken"];
      if (navIsActive) {
        if (!this.$store.getters.getIsNavShow) {
          return `/misi`;
        }
        return `/kemitraan/${encodeURIComponent(token)}}`;
      } else {
        // TO ANDROID
        return "/android";
      }
    },
    isNavShow() {
      return this.$store.getters.getIsNavShow;
    },
  },
};
</script>

<style scoped>
.route-enter-from {
  opacity: 0;
  transform: translateY(-30px);
}

.route-leave-to {
  opacity: 0;
  transform: translateY(30px);
}

.route-enter-active {
  transition: all 0.3s ease-out;
}

.route-leave-active {
  transition: all 0.3s ease-in;
}

.route-enter-to,
.route-leave-from {
  opacity: 1;
  transform: translateY(0);
}
</style>