import { createWebHistory, createRouter } from "vue-router";

import KemitraanPages from "./pages/KemitraanPages.vue";
import DownlinePages from "./pages/DownlinePages.vue";
import LevelMitra from "./pages/LevelMitraPages.vue";
import MediaPromosi from "./pages/MediaPromosiPages.vue";
import MisiPages from "./pages/MisiPages.vue";
import DetailMisiPages from "./pages/DetailMisiPages.vue";
import NotFound from "./pages/NotFound.vue";

const router = createRouter({
  history: createWebHistory(),
  routes: [
    {
      path: "/kemitraan/:token",
      name: "Kemitraan",
      component: KemitraanPages,
    },
    {
      path: "/downline",
      name: "Downline",
      component: DownlinePages,
    },
    {
      path: "/level-mitra",
      name: "LevelMitra",
      component: LevelMitra,
    },
    {
      path: "/media-promosi",
      name: "MediaPromosi",
      component: MediaPromosi,
    },
    {
      path: "/misi",
      name: "Misi",
      component: MisiPages,
    },
    {
      path: "/detail-misi/:params",
      name: "Detail Misi",
      component: DetailMisiPages,
      props: true,
    },
    {
      path: "/:notFound(.*)",
      component: NotFound,
    },
  ],
});

export default router;
