<template>
  <section id="kemitraan">
    <div
      class="vh-90 pb-5"
      style="overflow-y: scroll !important; overflow-x: hidden !important"
    >
      <base-toast
        :type="type"
        :message="message"
        :visibility="notification"
        @close="closeNotification"
      ></base-toast>
      <access-card @open="open" :loading="isLoading"></access-card>
      <what-is-mura></what-is-mura>
      <how-to-get-xp></how-to-get-xp>
      <benefit-xp></benefit-xp>
      <level-kemitraan></level-kemitraan>
      <information-xp></information-xp>
      <faq-component></faq-component>
      <agreement-and-contact></agreement-and-contact>
    </div>
  </section>
</template>

<script>
import AccessCard from "../components/kemitraan/AccessCard.vue";
import WhatIsMura from "../components/kemitraan/WhatIsMura.vue";
import HowToGetXp from "../components/kemitraan/HowToGetXp.vue";
import BenefitXp from "../components/kemitraan/BenefitXp.vue";
import LevelKemitraan from "../components/kemitraan/LevelKemitraan.vue";
import InformationXp from "../components/kemitraan/InformationXp.vue";
import FaqComponent from "../components/kemitraan/FaqComponent.vue";
import AgreementAndContact from "../components/kemitraan/AgreementAndContact.vue";

export default {
  components: {
    AccessCard,
    WhatIsMura,
    HowToGetXp,
    BenefitXp,
    LevelKemitraan,
    InformationXp,
    FaqComponent,
    AgreementAndContact,
  },
  data() {
    return {
      type: "",
      message: "",
      isLoading: false,
      notification: false,
    };
  },
  methods: {
    async getMitra() {
      this.isLoading = true;
      try {
        await this.$store.dispatch("mitra/getMitra");
      } catch (error) {
        this.type = "error";
        this.notification = true;
        this.message = error.message;
      }
      this.isLoading = false;
    },
    open() {
      this.type = "success";
      this.message = "Sukses Mengambil Data!";
      this.notification = true;
    },
    closeNotification() {
      this.type = "";
      this.message = "";
      this.notification = false;
    },
  },
  created() {
    this.$store.dispatch("setHeader", "Informasi Kemitraan");
    this.$store.dispatch("setNavTabIsActive", false);
    this.$store.dispatch("setIsNavShow", true);

    // SET TOKEN
    const token = this.$route.params.token;
    localStorage.setItem("token", token);
    this.$store.dispatch("setToken", decodeURIComponent(token));

    this.getMitra();
  },
};
</script>