<template>
  <section id="detail-misi">
    <div class="d-flex align-items-center vh-90" v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <div
      class="vh-90 pb-5"
      style="overflow-y: scroll !important; overflow-x: hidden !important"
      v-else
    >
      <base-toast
        :type="type"
        :message="message"
        :visibility="notification"
        @close="closeNotification"
      ></base-toast>
      <title-misi></title-misi>
      <check-misi></check-misi>
      <snk-misi></snk-misi>
      <footer-misi :status="statusEvent"></footer-misi>
    </div>
  </section>
</template>

<script>
import TitleMisi from "../components/detailquest/TitleMisi.vue";
import CheckMisi from "../components/detailquest/CheckMisi.vue";
import SnkMisi from "../components/detailquest/SnkMisi.vue";
import FooterMisi from "../components/detailquest/FooterMisi.vue";
export default {
  components: {
    TitleMisi,
    CheckMisi,
    SnkMisi,
    FooterMisi,
  },
  props: ["params"],
  data() {
    return {
      type: "",
      message: "",
      isLoading: false,
      notification: false,
      statusEvent: "",
    };
  },
  methods: {
    async getDetailMission(params) {
      this.isLoading = true;
      try {
        await this.$store.dispatch("misi/getDetailMission", params);
      } catch (error) {
        this.type = "error";
        this.message = error.message;
        this.notification = true;
      }
      this.isLoading = false;
    },
    closeNotification() {
      this.type = "";
      this.message = "";
      this.notification = false;
    },
  },
  created() {
    this.$store.dispatch("setHeader", "Detail Misi");
    this.$store.dispatch("setNavTabIsActive", true);
    this.$store.dispatch("setIsNavShow", false);
    this.$store.dispatch("checkIsLogin");

    const decodeParams = decodeURIComponent(this.params);
    const result = JSON.parse(decodeParams);
    this.statusEvent = result.status_event;
    this.getDetailMission(result);
  },
};
</script>