<template>
  <section id="downline" class="container">
    <div
      class="
        vh-90
        d-flex
        flex-column
        align-items-center
        justify-content-center
        gap-2
        p-2
      "
    >
      <img :src="image" alt="404" class="img-fluid" loading="lazy" />
    </div>
  </section>
</template>

<script>
import NotFound from "@/assets/icon/ilustration/404.svg";

export default {
  computed: {
    image() {
      return NotFound;
    },
  },
  created() {
    this.$store.dispatch("setHeader", "Not Found");
    this.$store.dispatch("setNavTabIsActive", true);
    this.$store.dispatch("setIsNavShow", true);
    this.$store.dispatch("checkIsLogin");
  },
};
</script>