<template>
  <base-spinner v-show="isLoading"></base-spinner>
  <base-toast
    :type="type"
    :message="message"
    :visibility="notification"
    @close="closeNotification"
  ></base-toast>
  <div id="content" v-show="!isLoading">
    <div class="container">
      <div class="row">
        <div class="col-12 mb-2">
          <h5 class="text-orange fw-700 lh-1 text-center">
            Levelmu Saat ini :
          </h5>
        </div>
      </div>
    </div>
    <swiper
      :navigation="true"
      :modules="modules"
      class="mySwiper"
      @swiper="onSwiper"
    >
      <swiper-slide
        class="px-2 position-relative mb-5"
        style="display: block"
        v-for="item in level"
        :key="item.id_level"
      >
        <img :src="item.image_level" class="" alt="level" loading="lazy" />
        <div class="explain mt-3">
          <div class="row">
            <div class="col-12">
              <h5 class="tex-center fw-700 text-orange">Benefit:</h5>
            </div>
            <div
              class="mx-5"
              v-for="(benefit, index) in item.benefit"
              :key="index"
            >
              <!-- di loop pakai modulo -->
              <!-- jika hasil bagi = 0 maka image di sebelah kiri -->
              <div class="kiri" v-if="index % 2 === 0">
                <div class="row gx-2">
                  <div class="col-2 mb-2 d-flex align-self-center">
                    <img
                      :src="benefit.benefit_icon"
                      alt=""
                      class="icon-getxp"
                      loading="lazy"
                    />
                  </div>
                  <div
                    class="
                      col-7
                      lh-sm
                      fw-500
                      font-small
                      d-flex
                      align-self-center
                      justify-content-start
                      text-dark-blue
                      mb-2
                      px-2
                    "
                  >
                    <p class="my-0">
                      <!-- Sukses melakukan transaksi <br />
                    produk di aplikasi Murapay. -->
                      {{ benefit.benefit }}
                    </p>
                  </div>
                </div>
              </div>
              <div class="kanan" v-else>
                <div class="row gx-2">
                  <div
                    class="
                      col-7
                      lh-sm
                      fw-500
                      font-small
                      d-flex
                      align-self-center
                      justify-content-end
                      text-dark-blue
                      mb-2
                      px-2
                    "
                  >
                    <p class="my-0 text-end">
                      <!-- Sukses melakukan transaksi <br />
                    produk di aplikasi Murapay. -->
                      {{ benefit.benefit }}
                    </p>
                  </div>
                  <div class="col-2 mb-2 d-flex align-self-center">
                    <img
                      :src="benefit.benefit_icon"
                      alt=""
                      class="icon-getxp"
                      loading="lazy"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/navigation";

// import required modules
import { Navigation } from "swiper";
export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data: function () {
    return {
      modules: [Navigation],
      type: "",
      message: "",
      isLoading: false,
      notification: false,
      swiper: null,
      timeout: 0,
    };
  },
  computed: {
    level() {
      return this.$store.getters["level/getLevel"];
    },
    position() {
      return this.$store.getters["level/getPosition"];
    },
  },
  methods: {
    onSwiper(swiper) {
      this.swiper = swiper;
    },
    async getLevel() {
      this.isLoading = true;
      try {
        await this.$store.dispatch("level/getLevel");
      } catch (error) {
        this.type = "error";
        this.message = error.message;
        this.notification = true;
      }
    },
    toTheActiveSlide() {
      this.swiper.slideTo(this.position, 0);
    },
    closeNotification() {
      this.type = "";
      this.message = "";
      this.notification = false;
    },
  },
  created() {
    this.getLevel();
  },
  mounted() {
    this.timeout = setTimeout(() => {
      // console.log(this.swiper.browser);
      this.toTheActiveSlide();
      this.isLoading = false;
    }, 3000);
  },
  beforeUpdate() {
    clearTimeout(this.timeout);
    this.timeout = 0;
  },
};
</script>