            <div class="d-flex align-self-center py-5">
              <h6 class="fw-bold text-center text-light-gray">
                Kamu Belum mengikuti misi apapun :( Ayo segera cek misi yang
                tersedia!
              </h6>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import xp_image from "@/assets/icon/get-xp/ic_xp.png";
export default {
  props: ["loading"],
  computed: {
    availableMission() {
      return this.$store.getters["misi/getMissionAvailable"];
    },
    subscribedMission() {
      return this.$store.getters["misi/getMissionSubscribed"];
    },
    hasAvailableMission() {
      return this.availableMission.length > 0;
    },
    hasSubscribedMission() {
      return this.subscribedMission.length > 0;
    },
    xpImage() {
      return xp_image;
    },
  },
  methods: {
    toDetail(id, type, status) {
      const params = {
        id_event: id,
        type_event: type,
        status_event: status,
      };
      const encodedParams = encodeURIComponent(JSON.stringify(params));
      this.$router.push(`/detail-misi/${encodedParams}`);
    },
  },
};
</script>