import axios from "axios";
import { configUrl } from "@/store/setup/config.js";

export default {
  namespaced: true,
  state() {
    return {
      mission: [],
      selectedMission: null,
    };
  },
  getters: {
    // getMission: (state) => state.mission,
    getSelectedMission: (state) => state.selectedMission,
    getListCheck: (state) => {
      const { selectedMission } = state;
      if (selectedMission) {
        const { detail } = selectedMission;
        return detail;
      } else {
        return {
          data: [],
        };
      }
    },
    getTitleMission: (state) => {
      const { selectedMission } = state;
      if (selectedMission) {
        return {
          nama: selectedMission.nama ? selectedMission.nama : "-",
          xp: selectedMission.xp ? selectedMission.xp : "-",
          periode: selectedMission.periode ? selectedMission.periode : "-",
          tipeMisi: selectedMission.tipe_mission ? selectedMission.tipe_mission : "-",
          bannerEvent: selectedMission.banner_event ? selectedMission.banner_event : "-",
        };
      } else {
        return {
          nama: "-",
          xp: "-",
          periode: "-",
          tipeMisi: "-",
        };
      }
    },
    getMissionAvailable: (state) => {
      return state.mission.filter((item) => item.status === "Avail Mission");
    },
    getMissionSubscribed: (state) => {
      return state.mission.filter((item) => item.status === "Subscribe Mission");
    },
  },
  mutations: {
    setMission(state, mission) {
      state.mission = mission;
    },
    setSelectedMission(state, mission) {
      state.selectedMission = mission;
    },
  },
  actions: {
    async getMission(context) {
      const url = configUrl("mission");

      const sendData = {
        wtinkouridol: context.rootGetters.getToken,
      };

      const response = await axios.post(url, JSON.stringify(sendData), {
        headers: {
          "Content-Type": "application/json",
        },
      });

      const responseData = response.data.data;

      if (response.data.status !== 200) {
        const error = new Error(responseData.message || "Gagal mendapatkan data misi!");
        throw error;
      }

      context.commit("setMission", responseData);
    },
    async getDetailMission(context, parameter) {
      const url = configUrl("detailmission");

      const sendData = {
        wtinkouridol: context.rootGetters.getToken,
        ...parameter,
      };

      const response = await axios.post(url, JSON.stringify(sendData), {
        headers: {
          "Content-Type": "application/json",
        },
      });

      const responseData = response.data;

      if (response.status !== 200) {
        const error = new Error(responseData.message || "Gagal mendapatkan detail misi!");
        throw error;
      }

      context.commit("setSelectedMission", responseData);
    },
  },
};
