<template>
  <div id="access-card">
    <!-- img header -->
    <div class="container my-3">
      <div class="row">
        <div class="col-12">
          <div class="d-flex justify-content-center mx-5">
            <img
              :src="logoMitraMura"
              alt="logo"
              class="img-fluid"
              loading="lazy"
            />
          </div>
        </div>
      </div>
    </div>

    <!-- Judul -->
    <div class="container my-3">
      <div class="row">
        <div class="col-12">
          <h5 class="color-dark-blue fw-bolder text-center d-block mb-0">
            Selamat bergabung di MitraMura!
          </h5>
        </div>
      </div>
    </div>

    <base-spinner v-if="loading" />
    <!-- Card -->
    <div class="container my-3 px-0" v-else>
      <div class="row mx-1">
        <div class="col-12 bg-access-card border-radius-20 p-3">
          <div class="row">
            <div class="col-8">
              <h6 class="text-light mb-0 lh-xs ps-1">
                {{ mitra.nama }} <br />
                <span class="text-light fst-italic font-small fw-normal"
                  >Bergabung sejak {{ mitra.tglDaftar }}</span
                >
              </h6>
              <!-- Form -->
              <div class="row me-5 mb-2">
                <div class="col-12 lh-xs">
                  <label
                    for="kode-mitra"
                    class="text-white font-extra-small fst-italic ps-1 fw-500"
                    >Kode Mitra:
                  </label>
                  <input
                    type="text"
                    name="kode-mitra"
                    id="kode-mitra"
                    class="
                      w-100
                      ps-2
                      border-blue
                      rounded-3
                      color-dark-blue
                      fw-bold
                      font-small
                    "
                    :value="mitra.kodeAgen"
                    readonly
                  />
                </div>
                <div class="col-12 lh-xs">
                  <label
                    for="jumlah-mitra"
                    class="text-white font-extra-small fst-italic ps-1 fw-500"
                    >Jumlah Mitra:
                  </label>
                  <input
                    type="text"
                    name="jumlah-mitra"
                    id="jumlah-mitra"
                    class="
                      w-100
                      ps-2
                      border-blue
                      rounded-3
                      color-dark-blue
                      fw-bold
                      font-small
                    "
                    :value="mitra.jmlMitra"
                    readonly
                  />
                </div>
                <div class="col-12 lh-xs">
                  <label
                    for="nomor-hp"
                    class="text-white font-extra-small fst-italic ps-1 fw-500"
                    >Nomor HP Terdaftar:
                  </label>
                  <input
                    type="text"
                    name="nomor-hp"
                    id="nomor-hp"
                    class="
                      w-100
                      ps-2
                      border-blue
                      rounded-3
                      color-dark-blue
                      fw-bold
                      font-small
                    "
                    :value="mitra.nomorHp"
                    readonly
                  />
                </div>
                <div class="col-12 lh-xs">
                  <span
                    class="text-white font-extra-small fst-italic ps-1 fw-500"
                    >XP Saat Ini : {{ currentXp }}</span
                  >
                </div>
              </div>
              <!-- Progress Bar -->
              <div class="row lh-xs">
                <div class="col-12">
                  <div
                    class="
                      text-center text-white
                      fst-italic
                      font-extra-small
                      mb-1
                    "
                  >
                    {{ isHold }}
                  </div>
                  <div class="progress" style="height: 45%; width: 97%">
                    <div
                      class="
                        progress-bar progress-bar-striped progress-bar-animated
                        background-orange
                      "
                      role="progressbar"
                      aria-label="Warning example"
                      :style="calculatePosition"
                      :aria-valuenow="calculatePosition"
                      aria-valuemin="0"
                      aria-valuemax="100"
                    ></div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-4 px-0">
              <div class="position-relative w-100 h-100">
                <img
                  :src="mitra.image"
                  alt="warga"
                  class="position-absolute translate-middle"
                  style="left: 35%; top: 50%"
                  loading="lazy"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container my-3">
      <div class="row">
        <div class="col-12 mb-1">
          <p class="text-center font-small color-dark-blue">
            Tampilan di atas adalah tampilan <i>"Access Card"</i> dari level
            kemitraanmu di program MitraMura pada saat ini.
          </p>
        </div>
        <div class="col-12">
          <div class="d-flex justify-content-center">
            <button
              class="
                btn btn-sm
                background-dark-blue
                text-white
                font-extra-small
                fw-bolder
              "
              @click="openModal"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                class="bi bi-cloud-arrow-down-fill"
                viewBox="0 0 16 16"
              >
                <path
                  d="M8 2a5.53 5.53 0 0 0-3.594 1.342c-.766.66-1.321 1.52-1.464 2.383C1.266 6.095 0 7.555 0 9.318 0 11.366 1.708 13 3.781 13h8.906C14.502 13 16 11.57 16 9.773c0-1.636-1.242-2.969-2.834-3.194C12.923 3.999 10.69 2 8 2zm2.354 6.854-2 2a.5.5 0 0 1-.708 0l-2-2a.5.5 0 1 1 .708-.708L7.5 9.293V5.5a.5.5 0 0 1 1 0v3.793l1.146-1.147a.5.5 0 0 1 .708.708z"
                />
              </svg>
              Unduh Access Card
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import logoMitraMura from "@/assets/images/logo_mitramura.png";

export default {
  computed: {
    logoMitraMura() {
      return logoMitraMura;
    },
    mitra() {
      return this.$store.getters["mitra/getMitra"];
    },
    currentXp() {
      return this.$store.getters["mitra/getCurrentXp"];
    },
    calculatePosition() {
      const position = this.$store.getters["mitra/getPositionXp"];
      // return width css
      return `width: ${position}%`;
    },
    isHold() {
      return this.$store.getters["mitra/isHold"];
    },
  },
  props: ["loading"],
  emits: ["open"],
  methods: {
    openModal() {
      this.$emit("open");
    },
  },
};
</script>