import { createStore } from "vuex";
import AuthModules from "./modules/auth";
import NavigationModules from "./modules/navigation";
import MitraModules from "./modules/mitra";
import MisiModules from "./modules/misi";
import LevelModules from "./modules/level";

const store = createStore({
  modules: {
    auth: AuthModules,
    navigation: NavigationModules,
    mitra: MitraModules,
    misi: MisiModules,
    level: LevelModules,
  },
});

export default store;
