<template>
  <section id="misi">
    <div
      class="vh-90 pb-5"
      style="overflow-y: scroll !important; overflow-x: hidden !important"
    >
      <base-toast
        :type="type"
        :message="message"
        :visibility="notification"
        @close="closeNotification"
      ></base-toast>
      <whatis-misi></whatis-misi>
      <status-misi :loading="isLoading"></status-misi>
    </div>
  </section>
</template>

<script>
import WhatisMisi from "../components/quest/WhatIsMisi.vue";
import StatusMisi from "../components/quest/StatusMisi.vue";
export default {
  components: {
    WhatisMisi,
    StatusMisi,
  },
  data() {
    return {
      type: "",
      message: "",
      isLoading: false,
      notification: false,
    };
  },
  methods: {
    async getMission() {
      this.isLoading = true;
      try {
        await this.$store.dispatch("misi/getMission");
      } catch (error) {
        this.type = "error";
        this.message = error.message;
        this.notification = true;
      }
      this.isLoading = false;
    },
    closeNotification() {
      this.type = "";
      this.message = "";
      this.notification = false;
    },
  },
  created() {
    this.$store.dispatch("setHeader", "Misi");
    this.$store.dispatch("setNavTabIsActive", true);
    this.$store.dispatch("setIsNavShow", true);
    this.$store.dispatch("checkIsLogin");

    this.getMission();
  },
};
</script>